import React, { Component } from 'react'
import ParticleBoom from '@/components/particleBoom'
import styles from './index.scss'
const pressure = require('@/assets/pressure.jpg')

export default class Home extends Component {
	render() {
		return (
			<div className={styles.homeContainer}>
				<h1 className={styles.title}>拥抱温暖, 传递爱心</h1>
				<img src={pressure} className={styles.pressure} />
				<ParticleBoom onClick={() => this.props.scrollPage(1)}>
					<div className={styles.action}>pressure.rar</div>
				</ParticleBoom>
			</div>
		)
	}
}


