import React, { Component } from 'react'
import themePicUrl from '@/assets/theme.png'
import styles from './index.scss'

const textList = [
	'在这个初秋的安静周六，还在加班的小伙伴散落在各个楼层，不同BU，负重前行。',
	'他们有的心怀希翼，有的强忍泪水。他们有的惑于岔路，有的怀疑价值。',
	'我们想，这些加班的人都需要爱的抱抱。',
	'告诉他们，最近辛苦了，你已经很棒了。',
	'告诉他们，美丽的珍珠总是在痛苦中被磨砺出来的。',
	'告诉他们，坚持下去吧，光明终将来临。',
	'以一颗糖的名义，递一份鼓励，用糖填满这个下午。',
	'以一个拥抱的名义，传一份温暖，仿佛拥抱了整个春天。',
	'有时候，陌生人的关怀像一阵偶然吹过的轻风，能瞬间驱散心头的阴翳。',
	'园区里的小伙伴们素不相识，却为了一个共同的理想并肩前行。',
	'我们拥抱过的每一个人都是将来的自己。',
	'如果在某天，当我焦灼无助得加班时，希望你也能过来，给我一个鼓励的微笑，爱的拥抱。',
	'每一件小小的善举，不求回馈世人，只为给你一丝丝温度。',
]

export default class End extends Component {
	render() {
		return (
			<div className={styles.container}>
				<img src={themePicUrl} className={styles.theme} />
				<div className={styles.summaryContainer}>
					{textList.map((text, idx)=> (
						<span key={idx} className={styles.summary}>{text}</span>
					))}
				</div>
			</div>
		)
	}
}


