import React, { Component } from 'react';
import classNames from 'classnames'
import styles from './App.scss';
import Home from './pages/home'
import Video from './pages/video'
import TagCloud from './pages/tagCloud'
import PhotoGallery from './pages/photoGallery'
import End from './pages/end'

const HEIGHT = window.innerHeight;
const OFFSET = 50;

class App extends Component {

	constructor (props) {
		super(props)
		this.fullpage = React.createRef()
	}

	state = {
		style: {},
		currentPage: 1,
		active: false
	}

	componentDidMount () {
		this.scroll()
		this.handleTransitionEnd()
	}

	/**
	 * 翻页
	 * @param {number} offset 翻动的页数
	 */
	scrollPage = (offset) => {
		const { currentPage, active } = this.state
		const current = offset + currentPage
		if (current === currentPage || current < 1 || current > 5 || active) return
		this.setState({ currentPage: current, active: true })

		const translateY = (-current + 1) * HEIGHT
		const style = {
			transform: `translate3d(0px, ${translateY}px, 0px)`
		}

		this.setState({ style })
	}

	/**
	 * 监听滚动事件
	 */
	scroll = () => {
		window.document.addEventListener('mousewheel', e => {
			e.preventDefault()
			// >0 下滑 <0 上滑
			const isDown = e.deltaY > 0
			if (Math.abs(e.deltaY) >= OFFSET) {
				this.scrollPage(isDown ? 1 : -1)
			}
		})
	}

	/**
	 * 动画结束事件
	 */
	handleTransitionEnd = () => {
		this.fullpage.current.addEventListener('transitionend', e => {
			if (e && e.propertyName === 'transform') {
				this.setState({ active: false })
			}
		})
	}

	pagination = () => {
		const { currentPage } = this.state

		const activeClass = key => classNames({
			[styles.active]: key === currentPage
		})

		return (
			<div id="pagination" className={styles.pagination}>
				<ul>
					<li>
						<a
							className={activeClass(1)}
							onClick={() => {this.scrollPage(1 - currentPage)}}
						><span></span></a>
					</li>
					<li>
						<a
							className={activeClass(2)}
							onClick={() => {this.scrollPage(2 - currentPage)}}
						><span></span></a>
					</li>
					<li>
						<a
							className={activeClass(3)}
							onClick={() => {this.scrollPage(3 - currentPage)}}
						><span></span></a>
					</li>
					<li>
						<a
							className={activeClass(4)}
							onClick={() => {this.scrollPage(4 - currentPage)}}
						><span></span></a>
					</li>
					<li>
						<a
							className={activeClass(5)}
							onClick={() => {this.scrollPage(5 - currentPage)}}
						><span></span></a>
					</li>
				</ul>
			</div>
		)
	}

	render() {
		const style = {
			height: `${HEIGHT}px`
		}

		return (
			<div className={styles.container}>
				<div
					id="fullpage"
					ref={this.fullpage}
					className={styles.fullpage}
					style={this.state.style}
				>
					<section style={style}><Home scrollPage={this.scrollPage} /></section>
					<section style={style}><TagCloud /></section>
					<section style={style}><Video /></section>
					<section style={style}><PhotoGallery /></section>
					<section style={style}><End /></section>
				</div>
				{this.pagination()}
			</div>
		);
	}
}

export default App;



