import React, { Component } from 'react'
import { randomGenerator } from '@/utils'
import genCloud from './genCloud'
import tagMap from './tagMap'
import styles from './index.scss'

const palette = [
	'blueviolet', 'burlywood', 'cadetblue', 'chocolate', 'cornflowerblue', 'cirmson', 'dodgerblue',
	'darkblue', 'yellowgreen', 'tomato', 'teal', 'steelblue', 'slategrey', 'royalblue', 'orangered'
]
const genTagIndex = randomGenerator(0, Object.keys(tagMap).length - 1)
const genColorIndex = randomGenerator(0, palette.length - 1)

export default class TagClound extends Component {

	componentDidMount () {
		this.genTagElement()
	}

	/**
	 * 存储已经存放的标签 index
	 */
	idxMap = {}

	/**
	 * 所有的标签总数量
	 */
	TAG_COUNT = Object.keys(tagMap).length

	TOTAL_COUNT = tagMap.reduce((sum, tag) => {
		sum += tag.count
		return sum
	}, 0)

	/**
	 * 一个屏幕显示的 标签数量
	 */
	MAX_COUNT = this.TAG_COUNT

	/**
	 * 字体初始大小
	 */
	BASE_FONT_SIZE = this.TOTAL_COUNT * 12

	/**
	 * 清除屏幕上的标签
	 */
	clear = () => {
		this.idxMap = {}
	}

	/**
	 * 一次性生成 MAX_COUNT 个 tag index
	 */
	genTagIdxOnce = () => {
		while (1) {
			if (Object.keys(this.idxMap).length >= this.MAX_COUNT) break
			const idx = genTagIndex()
			if (this.idxMap[idx]) continue
			this.idxMap[idx] = true
		}
	}

	/**
	 * 生成标签的 html 节点
	 */
	genTagElement = () => {
		this.clear()
		this.genTagIdxOnce()

		const tags = Object.keys(this.idxMap).map(idx => tagMap[idx | 0]);
		const tagElements = tags.map(t => {
			const color = palette[genColorIndex()]
			const fontSize = Math.ceil(t.count / this.TOTAL_COUNT * this.BASE_FONT_SIZE)
			return {
				text: t.text,
				size: fontSize,
				color: t.color || color,
				count: t.count
			}
		})

		const layout = genCloud(tagElements);
		layout.start()
	}

	render() {
		return (
			<div className={styles.tagContainer} id="tagContainer"></div>
		)
	}
}


