import React, { Component } from 'react'
import html2canvas from 'html2canvas'
import { ExplodingParticle } from '@/utils'
import styles from './index.scss'

export default class ParticleBoom extends Component {
	constructor (props) {
		super(props)
	}

	state = {
		particleCanvas: null,
		particleCtx: null,
		reductionFactor: 17,
		ctx: null,
		particles: [],
	}

	ref = React.createRef()

	componentDidMount () {
		window.onresize = this.resize;
		this.play()
		window.requestAnimationFrame(this.update);
	}

	play = () => {
		html2canvas(this.ref.current, { logging: false }).then(canvas => {
			const ctx = canvas.getContext('2d');
			this.setState({ ctx }, this.createParticleCanvas)
		})
	}

	handleClick = () => {
		const { ctx, reductionFactor } = this.state
		const width = this.ref.current.offsetWidth;
		const height = this.ref.current.offsetHeight;

		// 获取色值
		let colorData = ctx.getImageData(0, 0, width, height).data;

		let count = 0;

		// 遍历按钮的位置, 创建粒子
		for (let localX = 0; localX < width; localX++) {
			for (let localY = 0; localY < height; localY++) {
				if (count % reductionFactor === 0) {
					let index = (localY * width + localX) * 4;
					let rgbaColorArr = colorData.slice(index, index + 4);

					let bcr = this.ref.current.getBoundingClientRect();
					let globalX = bcr.left + localX + window.scrollX;
					let globalY = bcr.top + localY + window.scrollY;
					// 创建一个粒子
					this.createParticleAtPoint(globalX, globalY, rgbaColorArr);
				}
				count++;
			}
		}

		setTimeout(() => {
			this.props.onClick && this.props.onClick()
		}, 500)
	}

	createParticleAtPoint = (x, y, colorData) => {
		let { particles } = this.state
		let particle = new ExplodingParticle();
		particle.rgbArray = colorData;
		particle.startX = x;
		particle.startY = y;
		particle.startTime = Date.now();

		particles.push(particle)
		this.setState({ particles })
	}

	createParticleCanvas = () => {
		let particleCanvas = document.createElement('canvas');
		const particleCtx = particleCanvas.getContext('2d');

		// Size our canvas
		particleCanvas.width = window.innerWidth;
		particleCanvas.height = window.innerHeight;

		// Position out canvas
		particleCanvas.style.position = 'absolute';
		particleCanvas.style.top = '0';
		particleCanvas.style.left = '0';

		// Make sure it's on top of other elements
		particleCanvas.style.zIndex = '1001';

		// Make sure other elements under it are clickable
		particleCanvas.style.pointerEvents = 'none';

		this.setState({ particleCanvas, particleCtx })

		// Add our canvas to the page
		document.body.appendChild(particleCanvas);
	}

	update = () => {
		let { particleCtx, particles } = this.state
		// Clear out the old particles
		if (particleCtx) {
			particleCtx.clearRect(0, 0, window.innerWidth, window.innerHeight);
		}

		// Draw all of our particles in their new location
		for (let i = 0; i < particles.length; i++) {
			particles[i].draw(particleCtx);

			// 最后一个粒子完成动画, 清除旧的粒子
			if (i === particles.length - 1) {
				let percent =
					(Date.now() - particles[i].startTime) /
					particles[i].animationDuration;

				if (percent > 1) {
					particles = [];
				}
			}
		}

		this.setState({ particleCtx, particles }, () => {
			// Animate performantly
			window.requestAnimationFrame(this.update);
		})
	}

	resize = () => {
		let { particleCanvas } = this.state
		if (!particleCanvas) return;
		particleCanvas.width =
			window.innerWidth ||
			document.documentElement.clientWidth ||
			document.body.clientWidth;
		particleCanvas.height =
			window.innerHeight ||
			document.documentElement.clientHeight ||
			document.body.clientHeight;

		this.setState({ particleCanvas })
	}

	render() {
		return (
			<div
				id="particleContainer"
				className={styles.boom}
				ref={this.ref}
				onClick={this.handleClick}
			>
				{this.props.children}
			</div>
		)
	}
}


